import React, { useEffect, useRef, useState } from "react";
import "./index.css";
import { Button, List, message, Modal, Skeleton, Tag } from "antd";
import { useMount, useUnmount } from "ahooks";
import axios from "axios";

function SearchBar(props) {
  const { setHotWords, hotWords } = props;
  const [inputString, setInputString] = useState("");
  const [number, setNumber] = useState(1);
  const [relevantWords, setRelevantWords] = useState([]);
  const inputElement = useRef();
  const inputElement2 = useRef();

  const onBlur = (e) => {
    let hotWords = document.getElementById("hotwords");
    hotWords.style.transition = "filter 1s";
    hotWords.style.filter = "blur(0px)";
    let bg = document.getElementById("bg");
    bg.style.filter = "blur(0px)";
  };
  const handleSubmit = () => {
    if (!!inputString) {
      setHotWords((pre) => [
        ...pre,
        ...inputString.split("、").filter((item) => !!item),
      ]);
      setInputString("");
      inputElement.current.value = "";
    }
  };

  const handleChange = async (e) => {
    if (e.target.value !== "") {
      setInputString(e.target.value);
    } else {
      let historyWords = localStorage.getItem("historyWords");
      const arr = [...new Set(JSON.parse(historyWords))];
      if (historyWords) {
        setRelevantWords([...new Set(arr)]);
      }
    }
  };
  const handleNumberChange = async (e) => {
    if (e.target.value !== "") {
      setNumber(parseInt(e.target.value));
    }
  };

  useMount(() => {
    document.addEventListener("click", (ev) => {
      setRelevantWords([]);
    });
  });
  return (
    <div className="search" style={{ marginTop: "15px" }}>
      <div
        style={{
          width: "100%",
          marginBottom: "10px",
          display: "flex",
          justifyContent: "center",
        }}
      >
        <div className={"form"} id={"indexSearchBox"}>
          <input
            type="text"
            placeholder="请输入你要添加的选项..."
            // onFocus={onFocus}
            onBlur={onBlur}
            id={"indexInput"}
            ref={inputElement}
            onChange={handleChange}
            onKeyDown={(e) => {
              if (e.code === "Enter") {
                handleSubmit();
              }
            }}
            onClick={(e) => e.nativeEvent.stopImmediatePropagation()}
            autoComplete="off"
          />
          <Button
            type={"primary"}
            onClick={handleSubmit}
            style={{ height: "100%", marginLeft: "20px" }}
          >
            确定
          </Button>
          <Button
            type={"primary"}
            onClick={() => setHotWords([])}
            style={{ height: "100%", marginLeft: "20px" }}
          >
            清空选项
          </Button>
          {relevantWords.length === 0 ? (
            ""
          ) : (
            <div id={"word-complete"}>
              <List
                size="small"
                dataSource={relevantWords}
                split={false}
                onClick={(e) => {
                  e.nativeEvent.stopImmediatePropagation();
                  const str = e.target.textContent;
                  inputElement.current.value = str;
                  setInputString(str);
                  handleSubmit();
                }}
                renderItem={(item, index) => (
                  <List.Item
                    style={{
                      borderRadius: `${
                        index === 0 ? "10px 10px 0 0" : "0 0 0 0"
                      }`,
                    }}
                    className={"word-item"}
                  >
                    {item}
                  </List.Item>
                )}
              />
              {inputElement.current.value !== "" ? (
                <div />
              ) : (
                <Button
                  style={{ border: "0", float: "right", fontSize: ".8rem" }}
                  onClick={(e) => {
                    e.nativeEvent.stopImmediatePropagation();
                    localStorage.removeItem("historyWords");
                    setRelevantWords([]);
                  }}
                >
                  清除历史记录
                </Button>
              )}
            </div>
          )}
        </div>
      </div>
      <div style={{ width: "100%", display: "flex", justifyContent: "center" }}>
        <div className={"form"} id={"indexSearchBox"}>
          <input
            type="text"
            placeholder="请输入你要选择的选项个数..."
            // onFocus={onFocus}
            onBlur={onBlur}
            id={"indexInput"}
            ref={inputElement2}
            onChange={handleNumberChange}
            onClick={(e) => e.nativeEvent.stopImmediatePropagation()}
            autoComplete="off"
          />
          <Button
            type={"primary"}
            style={{ height: "100%", marginLeft: "20px" }}
            onClick={() => {
              if (hotWords.length === 0) {
                message.warning("选项为空！");
                return;
              }
              if (number > hotWords.length) {
                message.warning("选择个数超过选项个数！");
                return;
              }
              if (isNaN(number)) {
                message.warning("输入的数量无效！");
                return;
              }
              let res = [];
              let index = [];
              while (index.length < number) {
                const i = Math.floor(Math.random() * hotWords.length);
                if (!index.includes(i)) {
                  res.push(hotWords[i]);
                  index.push(i);
                }
              }
              Modal.info({
                content: (
                  <div>
                    <div>
                      您随机选取了
                      {res.map((item) => (
                        <Tag color={"green"}>{item}</Tag>
                      ))}
                    </div>
                    <br />
                    <div>全部的选项为：{hotWords.join("、")}</div>
                  </div>
                ),
                icon: null,
                style: { width: "80%", height: "80vh" },
              });
            }}
          >
            随机选择
          </Button>
        </div>
      </div>
    </div>
  );
}

export default SearchBar;
